body{margin:0}.header{display:flex;justify-content:space-between;align-items:center;background-color:#3A465C;padding:0 15%}.header button{margin:0}@media (max-width: 600px){.header{padding:0 0.5rem}}.main{margin:0 15%;display:flex;flex-direction:column}@media (max-width: 600px){.main{margin:0}}

.weekNav{display:flex;justify-content:space-between;align-items:center;margin:4rem auto;padding:1rem;box-sizing:border-box;background-color:#D2BEB0;border-radius:0.5rem;width:30vw;font-family:"Calligraffitti",cursive}.weekNav__arrow{font-size:2rem;padding:0.5rem;color:#3A465C;pointer-events:none}@media (max-width: 600px){.weekNav{width:100%;padding:0.5rem}.weekNav__arrow{width:1rem}}

.weekInfo{flex:1 1}.weekInfo__mainTitle,.weekInfo__subtitle{margin:0.5rem auto;text-align:center}.weekInfo__mainTitle{font-size:2rem;font-family:"Calligraffitti",cursive;color:#3A465C;margin:1rem auto}.weekInfo__subtitle{font-size:1.5rem;font-family:"Calligraffitti",cursive;color:#3A465C}.weekInfo__subtitle--day{display:none}@media (max-width: 600px){.weekInfo__mainTitle,.weekInfo__subtitle{margin:0}.weekInfo__subtitle--week{display:none}.weekInfo__subtitle--day{display:block}}

.button{font-family:"Calligraffitti",cursive;padding:0.5rem 1rem;box-sizing:border-box;border:none;border-radius:0.5rem;margin:auto;font-size:1.5rem}.button--dark{color:#D2BEB0;background-color:#3A465C}.button--dark:hover{color:#3A465C;background-color:#D2BEB0}.button--fair{color:#3A465C;background-color:#D2BEB0}.button--transparent{padding:0;background-color:transparent;outline:none}.button--invisibleOnDesktop{display:none}.button--disabled{background-color:#D2BEB0;color:#BB9684;cursor:not-allowed}.button__description{display:none}@media (max-width: 600px){.button--invisibleOnDesktop{display:block}}

.icon{pointer-events:none;flex:1 1;font-size:1.5rem;font-family:"Calligraffitti",cursive;color:#3A465C;font-size:1.2rem}

.tasksList{margin:1rem auto;text-align:center;border-collapse:collapse}.tasksList__title{font-size:1.5rem;font-family:"Calligraffitti",cursive;color:#3A465C;min-width:5rem}.tasksList__title:nth-of-type(odd){background-color:#D2BEB0}.tasksList__title button{width:100%;height:100%;padding:0.5rem;display:flex;justify-content:center}.tasksList__day{position:relative}.tasksList__day:nth-of-type(odd){background-color:#D2BEB0}.tasksList__row{border-bottom:#3A465C 1px dotted}.tasksList__task{font-size:1.2rem;font-family:"Montserrat",sans-serif;color:#000;padding:0.5rem;box-sizing:border-box;width:100%}@media (max-width: 600px){.tasksList{margin:1rem 0;width:100%}.tasksList__title--invisible,.tasksList__day--invisible{display:none}.tasksList__day{width:4rem}.tasksList__day:nth-of-type(odd),.tasksList__title:nth-of-type(odd){background-color:#fff}}.button__statusIcon{width:100%;height:100%;padding:0.2rem;display:flex;align-items:center;justify-content:center;position:absolute;top:0;left:0}.button__statusIcon--disabled{background-color:transparent}.button__sort{width:100%;height:100%;padding:0.5rem;display:flex;justify-content:center}.button__icon{padding:0 0 0 1rem;color:#BB9684}.button__icon--active{padding:0 0 0 1rem;color:#3A465C}

.emptyList{width:100%;text-align:center}.emptyList__title{font-size:2rem;font-family:"Calligraffitti",cursive;color:#3A465C;margin:1rem auto}.emptyList__text{font-size:1.2rem;font-family:"Montserrat",sans-serif;color:#000}.emptyList__icon{font-size:2rem;font-family:"Calligraffitti",cursive;color:#3A465C;margin:1rem auto;font-size:4.5rem;margin:0}

.loader{margin:0 auto;animation:spin 1s infinite linear;border:solid 2vmin transparent;border-radius:50%;border-right-color:#3A465C;border-top-color:#3A465C;box-sizing:border-box;height:20vmin;left:calc(50% - 10vmin);top:calc(50% - 10vmin);width:20vmin;z-index:1}.loader:before{animation:spin 2s infinite linear;border:solid 2vmin transparent;border-radius:50%;border-right-color:#BB9684;border-top-color:#BB9684;box-sizing:border-box;content:"";height:16vmin;left:0;position:absolute;top:0;width:16vmin}.loader:after{animation:spin 3s infinite linear;border:solid 2vmin transparent;border-radius:50%;border-right-color:#D2BEB0;border-top-color:#D2BEB0;box-sizing:border-box;content:"";height:12vmin;left:2vmin;position:absolute;top:2vmin;width:12vmin}@media (max-width: 600px){.loader{width:42vmin;height:42vmin;border-width:4vmin;top:calc(50% - 20vmin);left:calc(50% - 20vmin)}.loader:before{width:35vmin;height:35vmin;border-width:4vmin}.loader:after{width:27vmin;height:27vmin;top:4vmin;left:4vmin;border-width:4vmin}}@keyframes spin{100%{transform:rotate(360deg)}}

.backdrop{position:fixed;top:0;left:0;height:100vh;width:100%;background-color:rgba(175,164,166,0.527);z-index:100}

.modal{position:absolute;z-index:500;background-color:white;width:60%;min-height:70vh;border:1px solid #ccc;box-shadow:1px 1px 1px black;left:20%;top:10%;box-sizing:border-box;transition:all 0.3s ease-out;transform:translateY(0);display:flex;flex-direction:column}.modal--invisible{opacity:0;transform:translateY(-100vh)}@media (max-width: 600px){.modal{width:90%;left:5%;height:90vh;top:5%}}

.addTaskForm{display:flex;flex-direction:column;width:70%;margin:1rem auto}.addTaskForm__label{font-family:"Montserrat",sans-serif;padding:1rem 0;font-size:1.2rem}.addTaskForm__text,.addTaskForm__textarea{border-radius:0.5rem;font-size:1rem;font-family:"Montserrat",sans-serif}.addTaskForm__text{height:2rem}.addTaskForm__textarea{height:10rem}@media (max-width: 600px){.addTaskForm{width:80%}}.button--dark{margin:2rem 0;width:-webkit-max-content;width:-moz-max-content;width:max-content;align-self:center}

.addTask{flex:1 1;display:flex;align-items:center}.addTask__button{align-self:flex-end;margin:1rem;width:2rem}.addTask__close{color:#3A465C}@media (max-width: 600px){.addTask__openForm{position:fixed;z-index:3;bottom:0;margin:0;width:100%;border-radius:0}}

.status{color:#D2BEB0;font-size:1.2rem;padding:0.5rem;display:block;border:1px solid transparent}.status:hover{border:#BB9684 1px solid;border-radius:50%}

.logo{font-size:2rem;font-family:"Calligraffitti",cursive;color:#3A465C;margin:1rem auto;margin:0.5rem 0;color:#D2BEB0}

.legend{position:fixed;right:-15.5rem;top:12%;display:flex;width:-webkit-max-content;width:-moz-max-content;width:max-content;margin:0}.legend__open{right:1%}.legend__symbol{text-align:center}.legend__description{font-size:1.2rem;font-family:"Montserrat",sans-serif;color:#000;font-size:1rem;text-align:left}.legend__head{font-size:1.5rem;font-family:"Calligraffitti",cursive;color:#3A465C;font-size:1.2rem;padding:0.5rem 0}.legend__title{display:none}.legend button{transform:rotate(270deg);height:inherit;margin:0}@media (max-width: 600px){.legend{position:relative;right:0;top:0;flex-direction:column;width:100%;padding:0 0 4rem 0;margin:2rem 0;background-color:#e5d9d0}.legend button{display:none}.legend__title{display:inline-block;font-size:1.5rem;font-family:"Calligraffitti",cursive;color:#3A465C;text-align:center}.legend__table{width:100%}.legend__description{text-align:center}}

